import React, { useState } from 'react'
import Wrapper from '../wrappers/Opinions'
import quoteMark from '../images/quote-mark.svg'
import sylwiaPiotreOpinions from '../images/sylwia-i-piotrek-opinie.webp'
import dominikaWojtekOpinions from '../images/dominika-i-wojtek-opinie.webp'
import olaOpinions from '../images/ola-opinie.webp'
import kasiaSzymek from '../images/kasia-i-szymek-opinie.webp'
import arrowRight from '../images/arrow-right.svg'
import arrowLeft from '../images/arrow-left.svg'

const Opinions = () => {
  const [opinion, setOpinion] = useState(0)
  
  const opinionsArray = [
    { 
      img: sylwiaPiotreOpinions,
      txt:'Kuba ogldam te zdjcia jak szalona. Jestem totalnie zakochana. Nie wiem czy i to ju mwiam, ale jeste fotografem, ktry wycignie pikne emocje z ludzi nawet gdy sta bd na baczno. Twoje fotografie chwytaj gboko za serce',
      name:'Sylwia & Piotrek'
    },
    {
      img: dominikaWojtekOpinions,
      txt: ' Nasza sesja brzuszkowa bya wspaniaa. Zdjcia zachwycaj wszystkich. A teraz czekamy na sesj maluszka.',
      name: 'Dominika & Wojtek'
    },
    {
      img: kasiaSzymek,
      txt: 'Jestemy pod wraeniem efektw z sesji narzeczeskiej. Zdjcia plenerowe s wspaniae, naturalne i przepenione emocjami. Kuba podczas sesji zachowywa si bardzo profesjonalnie jednoczenie dba o dobry nastrj i mi atmofser, dziki czemu bya to dla nas dobra zabawa. Fotografie bd stanowiy przepikn pamitkn na dugie lata. Dzikujemy.',
      name: 'Kasia & Szymon'
    },
    {
      img: olaOpinions,
      txt: 'Jestem zdania e od czasu do czasu warto si rozpieszcza i robi sobie jakie prezenty. Dlatego wanie z tego powodu zdecydowaam si na sesj u Kuby i zatrzymanie kilku piknych kadrw na duej. ',
      name: 'Ola'
    }
  ]
  const nextPic = () => {
    if(opinion < opinionsArray.length - 1){
      setOpinion(opinion + 1)
    } else {
      setOpinion(0)
    }
  }
  const prevPic = () => {
    if(opinion > 0 ) { 
      setOpinion(opinion - 1)
    } else {
      setOpinion(opinionsArray.length - 1)
    }
  }

  return (
    <Wrapper>
        <h1>OPINIE</h1>
        <div className='slider-container'>
          
          <div className='opinions-container'>
            <div 
              className="opinions-inner"
              style={{transform: `translateX(${-opinion * 100}%)`}}
            >
                {opinionsArray && opinionsArray.map((e, index) => {
                  return (
                    <div className='opinions-item' key={index}>
                      <div className="opinions-txt">
                        <img src={quoteMark} alt='' className='quote-img'/>
                        <p>
                          {e.txt}
                        </p>
                        <p>
                          {e.name}
                        </p>
                      </div>
                      <div className="opinions-img">
                        <img 
                            src={e.img}
                            alt=''
                        />
                      </div>
                    </div>
                  )
                })}
                
            </div>
          </div>
          
          <div className='arrows-container'>
          <img 
            src={arrowLeft} 
            alt='' 
            className='arrow arrow-left'
            onClick={prevPic}
          />
          <img 
            src={arrowRight} 
            alt=''
            className='arrow arrow-right'
            onClick={nextPic}
          /> 
          </div>     
        </div>
    </Wrapper>
  )
}

export default Opinions