import Wrapper from "../wrappers/Portfolio"
import { Banner, PortfolioNavbar, Footer, ContactRedirect } from '../components'
import bannerPortfolioLove from '../images/banner-portfolio-love.webp'
import contactRedirect from '../images/contact-redirect-2.webp'
import loveImages from "../images/portfolio/love"

const PortfolioLove = () => {
  return (
    <>
        <Wrapper>
        <Banner 
            txt='PORTFOLIO'
            img={bannerPortfolioLove}
            align='bottom'
        />
        <PortfolioNavbar />
        <section className="main-content">
            <h3>SESJE MIOSNE</h3>
            <p>Namitno, czue westchnicia i dotyk. Zdjciami opowiem histori Waszej mioci. W plenerze czy zaciszu waszego domu... Klimat sesji dostosowuje do Was i Waszego pomysu. Podczas zdj robi wszystko co w mojej mocy, abycie czuli si swobodnie. Ta sesja to celebracja Waszej mioci, wsplna przygoda, z ktrej powstanie wspaniaa pamitka na lata. Z pewnoci sprawdzi si jako prezent na Walentynki, prezent na rocznic lub prezent lubny.</p>
            <div className="pictures">
              {loveImages && loveImages.map((e, index) => {
                const setRow = index % 4
                return (
                  <img key={index} src={e} alt='' className={`row-${setRow}`}/>
                )
              })}
            </div>
        </section>
        </Wrapper>
        <ContactRedirect 
        img={contactRedirect}
        />
        <Footer />
    </>
  )
}

export default PortfolioLove