import Wrapper from "../wrappers/Offer"
import { Banner, AboutMe, Card, Footer } from '../components'
import bannerOffer from '../images/banner-offer.webp'
import offerWedding from '../images/offer-wedding.webp'
import offerLove from '../images/offer-love.webp'
import offerFamily from '../images/offer-family.webp'

const Offer = () => {
  return (
    <div>
      <Banner 
        img={bannerOffer}
        txt='OFERTA'
        align='top'
      />
      <Wrapper> 
        <p className="head-txt">ycie pokazao mi jak warto maj zdjcia. Kady etap z mojego dziecistwa by fotografowany i wywoywany do albumw rodzinnych. Kad sesj, kady reporta, bd kiedy oglday pokolenia Zachowaj w kadrze swoje ycie. Czas z drug powk, czas z rodzin nie wrci.</p>
        <AboutMe 
          h1='FOTOGRAFIA LUBNA'
          p1='lub to jeden z najwaniejszych momentw w Waszym yciu. Bd zaszczycony mc towarzyszy wam w tym wyjtkowym dniu. Kad par traktuj indywidualnie, dlatego ofert wysyam bezporednio na adres e-mail. Wystarczy zapyta o moj dostpno na termin Waszego lubu uzupeniajc formularz kontaktowy, aby po kilku chwilach otrzyma ofert. Cena repotray rni si w zalenoci od odlegoci, preferencji, roku i wybranego pakietu od 3 do 5 ty z.'
          img={offerWedding}
          link='/portfolio/wedding'
          linkTxt='Obejrzyj przykadowe sesje'

        />
        <AboutMe 
          h1='SESJE MIOSNE'
          p1='Do sesji miosnych czuj najwiksz sympati. Sesja miosna ma by czasem dla Was, chwil czuoci, bliskoci. Najpikniejsze kadry miosne, ktre wykonaem zaczynay si od sw my nie umiemy pozowa. Zadbam o stworzenie takiej atmosfery, ktra pozwoli wam si otworzy i spdzi mio czas. Zdjcia s produktem ubocznym. Wszystko jest dla Was. Po mojej stronie stoi pokierowanie Wami i zauwaenie potencjau, w tym co na pierwszy rzut oka jest bardzo zwyczajne.'
          img={offerLove}
          columnReverse={true}
          link='/portfolio/love'
          linkTxt='Obejrzyj przykadowe sesje'
        />
        <div className="cards-container">
          <Card 
            h1='PAKIET PODSTAWOWY'
            props={['1 h sesji', '10 uj w postaci elektronicznej', '10 odbitek premium 13x18 cm', 'Kade dodatkowe ujcie 35 z', 'Cena obowizuje do 15km od Tarnowa', 'Powyej 15km +1z za kady 1km', 'Realizacja 14 dni roboczych', 'Inwestujesz: 480 zotych']}
          />
          <Card 
            h1='PAKIET PREMIUM'
            props={['1 h sesji', '21 uj w postaci elektronicznej', '10 odbitek premium 13x18 cm', 'Kade dodatkowe ujcie 30 z', 'Cena obowizuje do 15km od Tarnowa', 'Powyej 15km +1z za kady 1km', 'Realizacja 14 dni roboczych', 'Inwestujesz: 770 zotych']}
          />
        </div>
        <AboutMe 
          h1='SESJE RODZINNE'
          p1='Sesja z udziaem rodziny to uwiecznienie momentw od narodzenia dziecka a po zatrzymanie w kadrze relacji pomidzy rodzicami, dziadkami, kuzynami. Jedno jest pewne, na pewno nie bdzie sztywno! Sesja ma by dobr zabaw dla dziecka! Wan chwil jest te czas oczekiwania na maluszka. Specjalnie z tej okazji przygotowaem pakiet DUO, dwch sesji w jednym pakiecie (sesja ciowa, sesja noworodkowa).'
          img={offerFamily}
          link='/portfolio/family'
          linkTxt='Obejrzyj przykadowe sesje'
        />
        <div className="cards-container">
          <Card 
            h1='PAKIET PODSTAWOWY'
            props={['1 h sesji', '10 uj w postaci elektronicznej', '10 odbitek premium 13x18 cm', 'Kade dodatkowe ujcie 35 z', 'Cena obowizuje do 15km od Tarnowa', 'Powyej 15km +1z za kady 1km', 'Realizacja 14 dni roboczych', 'Inwestujesz: 480 zotych']}
          />
          <Card 
            h1='PAKIET PREMIUM'
            props={['1 h sesji', '21 uj w postaci elektronicznej', '21 odbitek premium 13x18 cm', 'Kade dodatkowe ujcie 30 z', 'Cena obowizuje do 15km od Tarnowa', 'Powyej 15km +1z za kady 1km', 'Realizacja 14 dni roboczych', 'Inwestujesz: 770 zotych']}
          />
          <Card 
            h1='PAKIET DUO'
            props={['Dwie sesje jednogodzinne', '17 elektronicznych uj z sesji ciowej', '17 elektronicznych uj z sesji ciowej', 'Kade dodatkowe ujcie 30 z', 'Cena obowizuje do 15km od Tarnowa', 'Powyej 15km +1z za kady 1km', 'Realizacja 14 dni roboczych', 'Inwestujesz: 1300 zotych']}
          />
        </div>
      </Wrapper>
      <Footer />
    </div>
  ) 
}

export default Offer