import React from 'react'
import Wrapper from '../wrappers/TermsAndPrivacy'
import { Banner } from '../components'

const TermsAndPrivacy = () => {
  return (
    <Wrapper>
        <Banner 
            txt='REGULAMIN'
        />
        <section className='main-content'>
            <h3>POLITYKA PRYWATNOCI</h3>
            <p>
              Niniejsza Polityka prywatnoci reguluje sposb, w jaki kuba stepek pic gromadzi, wykorzystuje, utrzymuje i ujawnia informacje zebrane od uytkownika ze strony internetowej www.kubastepekpic.pl (zwanej dalej Stron). Niniejsza polityka prywatnoci dotyczy strony kuba stepek pic. Na potrzeby lepszego odbioru Polityki Prywatnoci termin uytkownik zastpiony zosta okreleniem Wy, Administrator  Ja. Termin RODO oznacza Rozporzdzenie Parlamentu Europejskiego i Rady (UE) 2016/679 z dnia 27 kwietnia 2016r. w sprawie ochrony osb fizycznych w zwizku z przetwarzaniem danych osobowych i w sprawie swobodnego przepywu takich danych oraz uchylenia dyrektywy 95/46/WE.        
            </p>
            <p>
              W dniu 25 maja 2018 roku rozpoczo swoje obowizywanie Rozporzdzenie Parlamentu Europejskiego i Rady (UE) 2016/679 z dnia 27 kwietnia 2016 r. w sprawie ochrony osb fizycznych w zwizku z przetwarzaniem danych osobowych (w skrcie RODO). RODO pomoe chroni prywatno i sposb przechowywania danych osobowych. Zabezpieczam Wasze dane takie jak np.: imi, nazwisko, adres e-mail, numer telefonu czy informacje dotyczce Waszego lubu czy sesji. W kadej chwili macie prawo wgldu do treci danych oraz dania ich poprawienia lub usunicia. Korzystanie ze Strony, przegldanie treci na niej umieszczonych nie wymaga podania adnych danych osobowych. Dane osobowe potrzebne s jedynie w przypadku chci kontaktu ze mn za porednictwem formularza kontaktowego lub adresu e-mail umieszczonego na Stronie w celu uzyskania oferty wsppracy  w tym przypadku podanie danych jest niezbdne.
            </p>
            <h3>POSTANOWIENIA OGLNE</h3>
            <p>
              1. Administratorem danych jest kuba stepek pic  z siedzib w Zalasowej. Ochrona danych odbywa si zgodnie z wymogami powszechnie obowizujcych przepisw prawa, a ich przechowywanie ma miejsce na zabezpieczonych serwerach.
            </p>
            <p>
              2. Szanuj prawo do prywatnoci i dbamy o bezpieczestwo danych.
            </p>
            <p>
              3. Przetwarzam nastpujce dane osobowe: imi, nazwisko, e-mail, numer telefonu, informacje dotyczce Waszego lubu/sesji.
            </p>
            <p>
              4. Dane osobowe bd przetwarzane w celu odpowiedzi na zapytanie skierowane przy uyciu formularza kontaktowego.
            </p>
            <p>
              5. Podanie danych osobowych jest dobrowolne, jednake brak wyraenia zgody na przetwarzanie danych uniemoliwia wiadczenie w/w usugi.
            </p>
            <p>
              6. Dane osobowe bd przechowywane przez okres 1 roku od daty wysania zapytania
            </p>
            <p>
              7. Przysuguje Wam prawo dania dostpu do treci danych osobowych, ich sprostowania, usunicia oraz prawo do ograniczenia ich przetwarzania. Ponadto take prawo do cofnicia zgody w dowolnym momencie bez wpywu na zgodno z prawem przetwarzania, prawo do przenoszenia danych oraz prawo do wniesienia sprzeciwu wobec przetwarzania danych osobowych,
            </p>
            <p>
              8. Posiadacie prawo wniesienia skargi do Prezesa Urzdu Ochrony Danych Osobowych.
            </p>
            <p>
              9. Dane osobowe bd przekazywane wycznie naszym podwykonawcom, tj. dostawcom usug informatycznych.
            </p>
            <p>
              Osobowe dane identyfikacyjne
            </p>
            <p>
              Mog zbiera osobowe dane identyfikacyjne od Uytkownikw na rne sposoby, w tym midzy innymi, gdy Uytkownicy odwiedzaj moj Stron, wypeniaj formularz kontaktowy oraz w zwizku z innymi dziaaniami, usugami, funkcjami lub zasobami udostpnianymi na mojej stronie. Uytkownicy mog zosta poproszeni o podanie danych takich jak imi, nazwisko, adres e-mail, numeru telefonu. Uytkownicy mog jednak odwiedza moj Stron anonimowo. Bd zbiera dane osobowe od uytkownikw tylko wtedy, gdy dobrowolnie przel mi takie informacje. Uytkownicy zawsze mog odmwi dostarczenia osobistych danych identyfikacyjnych, z tym, e moe to uniemoliwi im angaowanie si w pewne dziaania zwizane ze Stron.
            </p>
            <p>
              Nieosobowe dane identyfikacyjne
            </p>
            <p>
              Mog zbiera nieosobowe informacje identyfikacyjne na temat Uytkownikw, ilekro wchodz oni w interakcj z moj Stron. Nieosobowe dane identyfikacyjne mog obejmowa nazw przegldarki, rodzaj komputera i informacje techniczne na temat sposobu, w jaki Uytkownicy cz si z moj Stron, na przykad system operacyjny i wykorzystywani dostawcy usug internetowych oraz inne podobne informacje.
            </p>
            <p>
              Pliki Cookies przegldarki internetowej
            </p>
            <p>
              Moja Strona moe wykorzystywa pliki cookies w celu poprawy komfortu uytkowania.
            </p>
            <p>
              Pliki cookies (tzw. ciasteczka) stanowi dane informatyczne, w szczeglnoci pliki tekstowe, ktre przechowywane s w urzdzeniu kocowym Uytkownika Strony i przeznaczone s do korzystania ze stron internetowych Strony. Cookies zazwyczaj zawieraj nazw strony internetowej, z ktrej pochodz, czas przechowywania ich na urzdzeniu kocowym oraz unikalny numer. Podmiotem zamieszczajcym na urzdzeniu kocowym Uytkownika Strony pliki cookies oraz uzyskujcym do nich dostp jest operator serwisu kuba stepek pic.
            </p>
            <p>
              Pliki cookies wykorzystywane s w celu:
            </p>
            <p>
              dostosowania zawartoci stron internetowych Serwisu do preferencji Uytkownika oraz optymalizacji korzystania ze stron internetowych; w szczeglnoci pliki te pozwalaj rozpozna urzdzenie Uytkownika Serwisu i odpowiednio wywietli stron internetow, dostosowan do jego indywidualnych potrzeb;
            </p>
            <p>
              tworzenia statystyk, ktre pomagaj zrozumie, w jaki sposb Uytkownicy Strony korzystaj ze stron internetowych, co umoliwia ulepszanie ich struktury i zawartoci;
            </p>
            <p>
              W ramach Strony stosowane s dwa zasadnicze rodzaje plikw cookies: sesyjne oraz stae. Cookies sesyjne s plikami tymczasowymi, ktre przechowywane s w urzdzeniu kocowym Uytkownika do czasu opuszczenia strony internetowej lub wyczenia oprogramowania (przegldarki internetowej). Stae pliki cookies przechowywane s w urzdzeniu kocowym Uytkownika przez czas okrelony w parametrach plikw cookies lub do czasu ich usunicia przez Uytkownika.
            </p>
            <p>
              W ramach Strony stosowane s nastpujce rodzaje plikw cookies:
            </p>
            <p>
              1. niezbdne pliki cookies, umoliwiajce korzystanie z usug dostpnych w ramach Strony, np. uwierzytelniajce pliki cookies wykorzystywane do usug wymagajcych uwierzytelniania w ramach Strony;
            </p>
            <p>
              2. pliki cookies suce do zapewnienia bezpieczestwa, np. wykorzystywane do wykrywania naduy w zakresie uwierzytelniania w ramach Strony;
            </p>
            <p>
              3. wydajnociowe pliki cookies, umoliwiajce zbieranie informacji o sposobie korzystania ze stron internetowych Strony;
            </p>
            <p>
              4. funkcjonalne pliki cookies, umoliwiajce zapamitanie wybranych przez Uytkownika ustawie i personalizacj interfejsu Uytkownika, np. w zakresie wybranego jzyka lub regionu, z ktrego pochodzi Uytkownik, rozmiaru czcionki, wygldu strony internetowej itp.;
            </p>
            <p>
              5. reklamowe pliki cookies, umoliwiajce dostarczanie Uytkownikom treci reklamowych bardziej dostosowanych do ich zainteresowa.
            </p>
            <p>
              W wielu przypadkach oprogramowanie suce do przegldania stron internetowych (przegldarka internetowa) domylnie dopuszcza przechowywanie plikw cookies w urzdzeniu kocowym Uytkownika. Uytkownicy Strony mog dokona w kadym czasie zmiany ustawie dotyczcych plikw cookies. Ustawienia te mog zosta zmienione w szczeglnoci w taki sposb, aby blokowa automatyczn obsug plikw cookies w ustawieniach przegldarki internetowej bd informowa o ich kadorazowym zamieszczeniu w urzdzeniu Uytkownika Strony. W takim przypadku ograniczenia stosowania plikw cookies mog spowodowa, e niektre czci Strony mog nie dziaa poprawnie. Szczegowe informacje o moliwoci i sposobach obsugi plikw cookies dostpne s w ustawieniach oprogramowania (przegldarki internetowej).
            </p>
            <p>
              Pliki cookies zamieszczane w urzdzeniu kocowym Uytkownika Strony wykorzystywane mog by rwnie przez wsppracujcych z operatorem Strony reklamodawcw oraz partnerw.
            </p>
            <p>
              Jak korzystam z zebranych informacji
            </p>
            <p>
              kuba stepek pic gromadzi i wykorzystuje dane osobowe Uytkownikw do nastpujcych celw:
            </p>
            <p>
              1. Aby poprawi moj Stron
            </p>
            <p>
              Nieustannie d do udoskonalania swojej strony internetowej w oparciu o informacje i opinie, ktre otrzymuj od Uytkownikw.
            </p>
            <p>
              2. Aby poprawi obsug klienta
            </p>
            <p>
              Informacje Uytkownikw pomagaj mi skuteczniej reagowa na zgoszenia dotyczce obsugi klienta i potrzeby pomocy technicznej.
            </p>
            <p>
              3. Aby administrowa treci, promocj lub inn funkcj witryny
            </p>
            <p>
              Informacje uytkownikw pozwalaj mi umieszcza na Stronie treci na tematy, ktre wydaje mi si, e bd dla nich interesujce.
            </p>
            <p>
              4. Aby wysya wiadomoci e-mail
            </p>
            <p>
              Adres e-mail Uytkownikw, ktry umoliwia przetwarzanie zamwie, bdzie wykorzystywany wycznie do wysyania im informacji i aktualizacji dotyczcych ich zamwienia. Moe rwnie suy do odpowiadania na ich zapytania i / lub inne proby lub pytania. Jeli w dowolnym momencie Uytkownik chce zrezygnowa z otrzymywania przyszych wiadomoci e-mail moe skontaktowa si ze mn za porednictwem mojej Strony.
            </p>
            <p>
              W jaki sposb chroni Twoje informacje
            </p>
            <p>
              Wykorzystuj odpowiednie metody gromadzenia, przechowywania i przetwarzania danych oraz rodki bezpieczestwa w celu ochrony przed nieuprawnionym dostpem, zmian, ujawnieniem lub zniszczeniem Twoich danych osobowych, nazwy uytkownika, hasa, informacji o transakcjach i danych przechowywanych na mojej Stronie. Wraliwa i prywatna wymiana danych midzy Stron a jej Uytkownikami odbywa si za porednictwem bezpiecznego kanau komunikacyjnego SSL i jest szyfrowana i chroniona za pomoc podpisw cyfrowych.
            </p>
            <p>
              Udostpnianie Twoich danych osobowych
            </p>
            <p>
             NIE sprzedaj, nie handluj ani nie wypoyczam osobowych danych identyfikacyjnych uytkownikw innym osobom. Mog udostpnia oglne zagregowane informacje demograficzne niezwizane z adnymi osobistymi danymi identyfikacyjnymi dotyczcymi odwiedzajcych Stron i uytkownikw z moimi partnerami biznesowymi, zaufanymi partnerami i reklamodawcami w celach opisanych powyej.
            </p>
            <p>
              Witryny stron trzecich
            </p>
            <p>
              Uytkownicy mog znale na mojej Stronie reklamy lub inne treci zawierajce linki do witryn i usug moich partnerw, dostawcw, reklamodawcw, sponsorw, licencjodawcw i innych stron trzecich. Nie kontroluj zawartoci ani linkw, ktre pojawiaj si w tych witrynach i nie ponosz odpowiedzialnoci za praktyki stosowane przez witryny internetowe powizane z moj Stron. Poza tym te witryny lub usugi, w tym ich tre i linki, mog cigle si zmienia. Te witryny i usugi mog mie swoj polityk prywatnoci i zasady obsugi klienta. Przegldanie i interakcja w jakiejkolwiek innej witrynie, w tym witrynach, ktre maj link do mojej Strony, podlega warunkom i zasadom tej witryny.
            </p>
            <p>
              Zmiany w niniejszej Polityce prywatnoci
            </p>
            <p>
              kuba stepek pic ma prawo do aktualizowania niniejszej Polityki prywatnoci w dowolnym momencie. Zachcam uytkownikw do czstego sprawdzania tej strony pod ktem wszelkich zmian, aby by na bieco z informacjami o tym, w jaki sposb pomagam chroni gromadzone przeze mnie dane osobowe. Uytkownik przyjmuje do wiadomoci i zgadza si, e jego obowizkiem jest okresowe przegldanie niniejszej Polityki prywatnoci i zapoznawanie si z jej modyfikacjami.
            </p>
            <p>
              Twoja akceptacja niniejszych warunkw
            </p>
            <p>
              Korzystajc z tej Strony, wyraasz zgod na te zasady. Jeli nie zgadzasz si z tymi zasadami, nie korzystaj z mojej Strony. Dalsze korzystanie z Witryny po opublikowaniu zmian w tych zasadach zostanie uznane za Twoj akceptacj tych zmian.
            </p>
            <p>
              Kontakt ze mn
            </p>
            <p>
              Jeli masz jakiekolwiek pytania dotyczce niniejszej Polityki prywatnoci, praktyk tej Strony lub Twoich powiza z t witryn, skontaktuj si ze mn na: kubastepekpic@gmail.com lub skorzystaj z mojego formularza kontaktowego.
            </p>


        </section>

    </Wrapper>
  )
}

export default TermsAndPrivacy