import {NavLink} from 'react-router-dom'
import Wrapper from "../wrappers/Landing"
import main1 from '../images/main-1.webp'
import main2 from '../images/main-2.webp'
import main3 from '../images/main-3.webp'
import kubaSmall1 from '../images/kuba-small-1.webp'
import kubaSmall from '../images/kuba-small.webp'
import portfolioNavImg1 from '../images/portfolio-nav-img-1.webp'
import portfolioNavImg2 from '../images/portfolio-nav-img-2.webp'
import portfolioNavImg3 from '../images/portfolio-nav-img-3.webp'
import offerNav from '../images/offer-nav.webp'
import kubaSmall2 from '../images/kuba-small-2.webp'
import contactRedirect from '../images/contact-redirect-1.webp'
import quoteMark from '../images/quote-mark.svg'
import { PortfolioNavImg, Opinions, ContactRedirect, Footer, Carousel } from "../components"

const Landing = () => {
   const slides = [main1, main2, main3]

  return (
    <Wrapper>
      <section className="main-view">
        <div className="main-img-container">
          <Carousel slides={slides}/>
        </div>
      </section>
      <section className="about-me">
        <div className="about-txt">
          <h3>KUBA STEPEK PIC</h3>
          <p>
            Mwi, e dusz artysty poznasz po tym, co tworzy. 
            Ja tworz moj ma galeri sztuki na podstawie Waszych emocji i historii ludzi, 
            z ktrymi jest mi dane wsppracowa. Kocham wykorzystywa swoje pomysy na 
            zachowanie piknych i wyjtkowych momentw penych wzrusze i umiechw. 
          </p>
          <p>
            Mam na imi Kuba i zapraszam Ci do mojego wiata fotografii.
          </p>
        </div> 
        <div className="portraits-container">
          <img
            className="portrait-img to-bottom"
            src={kubaSmall}
            alt=''
          />
          <img
            className="portrait-img to-top"
            src={kubaSmall1}
            alt=''
          /> 
        </div>
      </section>
      <section className="portfolio">
        <h3>PORTFOLIO</h3>
        <div className="portfolio-container">
          <PortfolioNavImg 
            img={portfolioNavImg1}
            txt={'FOTOGRAFIA LUBNA'}
            path='/portfolio/wedding'
          />
          <PortfolioNavImg 
            img={portfolioNavImg2}
            txt={'SESJE MIOSNE'}
            path='/portfolio/love' 
          />
          <PortfolioNavImg 
            img={portfolioNavImg3}
            txt={'SESJE RODZINNE'}
            path='/portfolio/family'
          />
        </div>
      </section>
      <section className="quote">
        <div>
          <img src={quoteMark} alt=''/>
          <p>Zdjcie to wyraanie wrae. Jeeli pikno nie bdzie istnie w nas samych, to jak w ogle bdziemy mogli je zauway?</p>
          <p>Ernst Haas</p>
        </div>
      </section> 
      <section className="offer-container">
        <img 
          src={offerNav}
          alt=''
        />
        <div className="offer-box">
          <h2>OFERTA</h2>
          <p>W zakadce oferta znajdziesz szczegy dotyczce fotografii lubnej (w tym reportay i sesji lubnej), sesji zakochanych oraz sesji rodzinnych.</p>
          <NavLink
            to='offer'
            className='btn btn-block'
          > 
            Zobacz ofert
          </NavLink>
        </div>
      </section>
      <div className='opinions-content'>
        <Opinions/> 
      </div>
      <section className="description-container">
        <img 
          src={kubaSmall2}
          alt=''
        />
        <div className="description">
          <p>Profesjonalny fotograf z maopolski.</p>
          <p className="txt-margin">Podczas mojej pracy jako fotograf spotykam wielu wspaniaych ludzi, odwiedzam wyjtkowe miejsca i uczestnicz w intymnych i wanych momentach w yciu. Miaem przyjemno wykonywa sesje miosne i narzeczeskie, brzuszkowe, narodzinowe, sesje i reportae lubne a take uwiecznia inne wane wydarzenia.</p>
          <p className="txt-margin">Pochodz z Zalasowej koo Tarnowa jednak jako fotograf mam okazj pracowa nie tylko w Tarnowie, ale take w okolicznych miejscowociach. Fotografowaem ju w takich miastach jak Tarnw, Krakw, Nowy Scz, Tuchw, Zalasowa, Zakliczyn, Brzesko, Bochnia, Ryglice.</p>
          <p className="txt-margin">Jeli wic szukasz fotografa na w Tarnowie, Krakowie lub okolicach to dobrze trafie! Zobacz ofert i skorzystaj z formularza kontaktowego.</p>
          <p className="txt-margin">Do zobaczenia! Kuba Stepek - fotografia maopolska</p>
          <small className="txt-margin">PS. Cho najczciej pracuj na terenie maopolski, to tak naprawd odlegoci nie maj znaczenia. Jeli mieszkacie dalej i chcecie, bym by Waszym fotografem  rwnie zapraszam do kontaktu.</small>
        </div>
      </section>
      <ContactRedirect 
        img={contactRedirect}
      />
      <Footer />
    </Wrapper>
  )
}

export default Landing