import Wrapper from "../wrappers/About"
import bannerAbout from '../images/banner-about.webp'
import kubaSmall3 from '../images/kuba-small-3.webp'
import kubaSmall4 from '../images/kuba-small-4.webp'
import grandfather from '../images/grandfather-small.webp'
import kubaSmall5 from '../images/kuba-small-5.webp'
import kubaSmall6 from '../images/kuba-small-6.webp'
import kubaSmall7 from '../images/kuba-small-7.webp'
import contactRedirect from '../images/contact-redirect-3.webp'
import { Banner, AboutMe,  ContactRedirect, Footer, Opinions } from "../components"

const About = () => {
  return (
    <Wrapper>
        <Banner 
          txt='O MNIE'
          img={bannerAbout}
          align='25% 25%'
        />
      <main>
        <AboutMe 
          h1='CZE, JESTEM KUBA'
          p1='Z wyksztacenia jestem fotografem z zakresu rejestracji, obrbki i publikacji obrazu, a swoj drog fotograficzn zaczem ju w wczesnych latach szkolnych. Mam na swoim koncie wiele sesji i reportay, podczas ktrych uwieczniaem najwaniejsze wydarzenia w yciu wielu wspaniaych ludzi.'
          p2='Natura jest bardzo bliska mojemu sercu, co czsto pokazuje w mojej twrczoci. Jestem te ogromnym fanem fotografii analogowej, filmowej stylistyce i wszystkiego, co zawiera w sobie klimat vintage.'
          p3='Kada kolejna sesja i repotra, s dla mnie okazj do pokazania moich umiejtnoci oraz wyraenia emocji, ktre towarzysz wam podczas sesji.'
          img={kubaSmall3}
        />
        <AboutMe 
          h1='3...2...1...PIC! czyli jak to si zaczo'
          p1='Co najbardziej mnie poruszyo do dziaania i zaprzyjanienia si z aparatem na duszy czas? Starsi ludzie Zwyczajny dzie na dziace u pradziadkw. Wystarczyo tak niewiele. Obok zdjcie, od ktrego wszystko si zacz. Ju wtedy wiedziaem, e to jest to. Takie zdjcia najbardziej uwiadamiaj przemijanie i pokazuj moc pamitki.'
          img={grandfather}
          columnReverse={true}
        />
        <AboutMe 
          h1='OD PASJI DO STYLU YCIA'
          p1='Fotografia jest moj najwiksz pasj. Od wielu lat nieustannie rozwijam si w tym kierunku, biorc udzia w kursach i szkoleniach. Jednym z takich krokw w przd byy warsztaty fotograficzne u Kasi Budzik i ju planuj kolejne kursy, ktre uksztatuj i uwiadami mi sporo kwestii na tej artystycznej drodze.'
          p2='Mwic o zainteresowaniach nie mog pomin tutaj muzyki  jednej z waniejszych dziedzin w moim yciu. piew, scena poruszaj jeszcze mocniej strun mojej wraliwoci i dopeniaj niekiedy moje szalone i zakrcone ycie.'
          p3='Jestem szczliwy, e mog realizowa si wykonujc moj pasj na codzie. Mam nadziej, e niebawem spotkamy si na sesji.'
          img={kubaSmall4}
        />
        <div className="portfolio-container">
          <img 
            className="small-img"
            src={kubaSmall5}
            alt=''
          />
          <img 
            className="small-img"
            src={kubaSmall6}
            alt=''
          />
          <img 
            className="small-img"
            src={kubaSmall7}
            alt=''
          />
        </div>
        <div className="info-txt">
          <h4>CHCESZ WIEDZIE WICEJ O MOJEJ PRACY I TWORZENIU?</h4>
          <h4>ZAPRASZAM CI NA ROZMOW I SESJ.</h4>
        </div>
        <Opinions />
      </main>
      <ContactRedirect 
        img={contactRedirect}
      />
      <Footer />
    </Wrapper>
  )
}

export default About 