import Wrapper from "../wrappers/Portfolio"
import { Banner, PortfolioNavbar, Footer, ContactRedirect } from '../components'
import bannerPortfolioWedding from '../images/banner-portfolio-wedding.webp'
import contactRedirect from '../images/contact-redirect-5.webp'
import weddingImages from "../images/portfolio/wedding"

const PortfolioWedding = () => {

  return (
    <>
      <Wrapper>
      <Banner 
        txt='PORTFOLIO'
        img={bannerPortfolioWedding} 
        align='bottom'
      />
      <PortfolioNavbar />
      <section className="main-content">
        <h3>FOTOGRAFIA LUBNA</h3>
        <p>Jestem wdziczny za to, e obdarzacie mnie swoim zaufaniem, abym dla Was zrobi zdjcia z wesela. W tym dniu powstaj najpikniejsze pamitki przepenione mioci i radoci. Poniej moecie znale moje ulubione kadry. Uwielbiam dobiera styl do charakteru uroczystoci. Zawsze dbam o to, by zdjcia lubne byy ze sob spjne i razem opowiaday jedn miosn histori. Decydujc si na wybr zdj do porfolio pozwoliem sobie na rnorodno, mieszajc moje ulubione kadry z rnych dni. Mam nadzij, e spord wszystkich opublikowanych zdj lubnych znajdziesz co dla siebie.</p>
        <div className="pictures">
          {weddingImages && weddingImages.map((e, index) => {
            const setRow = index % 4
            return (
              <img key={index} src={e} alt='' className={`row-${setRow}`}/>
            )
          })}
        </div>
      </section>
      </Wrapper>
      <ContactRedirect 
        img={contactRedirect}
      />
      <Footer />
    </>
  )
}

export default PortfolioWedding